<template>
	<view class="deal">
		<watermark></watermark>
		<!-- 顶部导航 -->
		<navTabs :list="list" @getChange="_getChange" @getData="_getData" @clickPrj="_clickPrj" :isTop="'false'"></navTabs>
		<view class="padiingTop60">
			<barDropDown :key='0' :chartData="chartData" :unitText="'%'" :timeUnit="form.timeUnit" :timeSelect="timeSelect"
			 @prevTap="_prevTap" @nextTap="_nextTap" @unitTap="_unitTap"></barDropDown>
		</view>
		<view class="express_box">
			<view class="express_list_wrap">
				<view class="express_list" v-for="(item,index) in tdList" :key="index">
					<view class="proIndex">{{index + 1}}</view>
					<view class="right_box">
						<view class="proName">{{item.projectName}}</view>
						<view class="bottom">
							<view class="bottom_item">
								<text>来访人数</text>
								<text>{{item.refundsAmount}}</text>
							</view>
						</view>
					</view>
				</view>
			</view>
		</view>
		
		
		
		<!-- <view class="tabs_item paddingBottom" v-if="current==0">
			<barDropDown :key='0' :chartData="chartData" :unitText="'%'" :timeUnit="form.timeUnit" :timeSelect="timeSelect"
			 @prevTap="_prevTap" @nextTap="_nextTap" @unitTap="_unitTap"></barDropDown>
			<view class="express_box">
				<utable :thList="thList" :tdList="tdList" :status="4"></utable>
			</view>
		</view>
		<view class="tabs_item paddingBottom" v-if="current==1">
			<barDropDown :key='0' :chartData="chartData" :unitText="'%'" :timeUnit="form.timeUnit" :timeSelect="timeSelect"
			 @prevTap="_prevTap" @nextTap="_nextTap" @unitTap="_unitTap"></barDropDown>
			<view class="express_box">
				<utable :thList="thList1" :tdList="tdList" :status="13"></utable>
			</view>
		</view> -->
		<!-- <view class="footer" @click="goBack">返回</view> -->
	</view>
</template>

<script>
	import watermark from '@/components/watermark.vue';
	import navTabs from '@/components/nav-tabs.vue';
	import barDropDown from '@/components/barDropDown.vue';
	import utable from '@/components/utable.vue';
	import httpUtils from '@/utils/httpUtils.js';
	import Tools from '@/utils/tools.js';
	import { bus } from '@/utils/bus.js';
	export default {
		data() {
			return {
				current: 0,
				list: [{
					name: '来访转认购'
				}, {
					name: '来访转签约'
				}],
				
				form: {
					companyId:'', // 公司id
					projectId:'', // 项目id
					tabCode: '', // tabs
					timeUnit: '2', // 时间统计 0年 1季 2月 3周
					time: '', // 当前统计格式 prev/next值
				},
				timeSelect: { // 柱状图选择
					prev: '',
					next: '',
				},
				etype: 0, //0第一次加载 1查询 2下拉刷新 3上拉加载更多
				page: {
					pageNum: 1,
					pageSize: 10
				},
				totalPages: 0, // 总页数
				loading: false,
				// 柱状图
				chartData: {
					categories: [],
					series: [{
						data: []
					}],
				},
				thList:[{name:'序号'},{name:'项目'},{name:'来访人数'},{name:'转化人数'},{name:'转化率'}],
				thList1:[{name:'序号'},{name:'项目'},{name:'来访人数'},{name:'签约人数'},{name:'转化率'}],
				tdList:[],
			}
		},
		methods: {
			// 获取列表
			getList() {
				let that = this
				that.form.tabCode = that.current == 0 ? 7 : 8
				Tools.showLoading('加载中')
				httpUtils.sendHttpRequest('3010', Object.assign({}, that.form, that.page), function(res) {
					Tools.hideLoading()
					uni.hideNavigationBarLoading()
					if (res.data.code == '0') {
						that.timeSelect.prev = res.data.data.histogramData.pre
						that.timeSelect.next = res.data.data.histogramData.next
						that.chartData.series[0].data = res.data.data.histogramData.yData
						that.$set(that.chartData, 'series', that.chartData.series)
						that.$set(that.chartData, 'categories', res.data.data.histogramData.xData)
						that.tdList = that.etype == 3 ? that.tdList.concat(res.data.data.tableData.list) : res.data.data.tableData.list
						that.totalPages = res.data.data.tableData.totalPage
						if (that.loading) {
							that.loading = false
							uni.stopPullDownRefresh()
						}
						bus.$emit('changeEcharts',that.chartData)
					}
				})
			},
			// 点击公司回调
			_getData(val, val2){
				this.form.companyId = val
				this.form.projectId = val2
				this.page.pageNum = 1
				this.tdList = []
				this.getList()
			},
			// 点击项目回调
			_clickPrj(val){
				this.form.projectId = val
				this.page.pageNum = 1
				this.tdList = []
				this.getList()
			},
			// 下拉刷新
			onPullDownRefresh() {
				this.page.pageNum = 1
				this.etype = 2
				if (!this.loading) {
					this.loading = true
					this.getList()
				}
			},
			// 上拉加载
			onReachBottom() {
				if (this.totalPages > this.page.pageNum) {
					this.etype = 3
					this.page.pageNum++
					this.flag = true
					this.getList()
				}
			},
			// tabs切换
			_getChange(index) {
				this.current = index
				this.page.pageNum = 1
				this.tdList = []
				this.form.time = ''
				this.getList()
			},
			// 柱状图左翻
			_prevTap() {
				this.form.time = this.timeSelect.prev
				this.etype = 0
				this.page.pageNum = 1
				this.getList()
			},
			// 柱状图右翻
			_nextTap() {
				this.form.time = this.timeSelect.next
				this.etype = 0
				this.page.pageNum = 1
				this.getList()
			},
			// 柱状图单位选择
			_unitTap(index) {
				this.form.timeUnit = index
				this.form.time = ''
				this.etype = 0
				this.page.pageNum = 1
				this.getList()
			},
			// 返回
			// goBack(){
			// 	uni.navigateBack({
			// 		delta:1
			// 	})
			// }
		},
		components: {
			watermark,
			navTabs,
			barDropDown,
			utable
		},
	}
</script>

<style lang="scss">
	page {
		background-color: #f6f6f6;
	}
</style>
